:root {
    --eggshell-white: #E3EBF8;
    --light-cream-white: #E1D9D1;
    --sea-green: #0F807C;
    --dark-cyan: #054D4A;
    --foam-green: #85D6CD;
    --text-gray: #2E3033;
    --sold-red: #CD4036;


    --white: #FFFFFF;
    --black: #000000;

}

*,
*::before,
*::after {
    margin: 0;
    padding: 0;
    box-sizing: inherit;
}

html {
    box-sizing: border-box;
    font-size: 62.5%;     // default 16px; 1rem = 10px --> 10px/16px = 62.5%
}

body {
    
    font-weight: 400;
    line-height: 1.6;
}

@font-face {
    font-family: "Condensed";
    src: local("Franklin Gothic Condensed"),
    url("./fonts/Franklin\ Gothic\ Condensed.ttf") format("truetype");
}

@font-face {
    font-family: "Normal";
    src: local("FRANKGO"),
    url("./fonts/FRANKGO.ttf") format("truetype");
}

@font-face {
    font-family: "Regular";
    src: local("FranklinGothic"),
    url("./fonts/FranklinGothic.ttf") format("truetype");
}

@font-face {
    font-family: "Italic";
    src: local("FranklinGothicITALIC"),
    url("./fonts/FranklinGothicITALIC.ttf") format("truetype");
}