.header {
    font-family: "Regular";
    background-color: rgba($color: #ffffff, $alpha: .85);
    display: flex;
    justify-content: space-between;
    height: 10rem;
    box-shadow: 0rem .5rem 3rem .1rem black;
    
}

.logo {
    height: 8rem;
    padding-left: 1rem;
    align-self: center;

    &:hover {
        cursor: pointer;
    }
}

.contact-icons-box {
    display: flex;
    flex-direction: column;
    justify-content: space-around;
    margin-left: auto;
    padding: 0 2rem;
}

.contact-icons {
    height: 3.5rem;
    

    &:hover {
        cursor: pointer;
    }
}

.hamburger-box {
    background-color: var(--foam-green);
    display: flex;
    padding: 0rem 2rem;
    border-left: .1rem solid #545454;
}

.hamburger-icon {
    font-size: 3rem;
    margin: auto;
    

    &:hover {
        cursor: pointer;
    }
}