.homeheading {
    display: flex;

    background-image:url('../../img/skyline-mobile.png');
    background-repeat: no-repeat;
    background-size: cover;
    height: 50rem;
    justify-content: center;
}

.homeheading-text {
    text-align: center;
    align-self: center;
    
    width: 25rem;
    height: 20rem;
    font-family: 'Franklin Gothic Book';

    font-style: normal;
    font-weight: 400;
    font-size: 22px;
    line-height: 25px;
    text-align: center;

    color: #E7F0F2;
    /* We SERVE Chicagoland homeowners in need of solutions, REBUILD neighborhood homes, and FULFILL family dreams */


    position: absolute;
    text-shadow: 0px 4px 4px rgba(190, 190, 190, 0.25);

}

.homeheading-text-altered {
    color: #00ADAD;
}

